/* eslint-disable no-unused-vars */
/* eslint-disable react/prop-types */
import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  TextField,
  withStyles,
  RadioGroup,
  FormControlLabel,
  Radio,
  Grid,
  CircularProgress
} from "@material-ui/core";

import styles from "./styles";
import { createTeam } from "../../store/modules/team/actions";

function ModalCreateTeam({ classes, open, setOpen }) {
  const dispatch = useDispatch();
  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const loading = useSelector(state => state.team.createTeamLoading);

  const [type, setType] = useState("Geral");

  function handleClose() {
    setOpen(false);
  }

  function handleSubmit(e) {
    e.preventDefault();

    dispatch(createTeam({ name, description, type }));
  }

  return (
    <div>
      <Dialog
        open={open}
        aria-labelledby="Modal join team"
        aria-describedby="Modal join team"
      >
        <DialogTitle id="alert-dialog-slide-title">
          Criar uma equipe
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-slide-description">
            Preencha os dados da sua nova equipe!
          </DialogContentText>
        </DialogContent>

        <DialogContent>
          <form onSubmit={e => handleSubmit(e)}>
            <TextField
              value={name}
              onChange={e => setName(e.target.value)}
              required
              margin="dense"
              id="name"
              label="Nome da sua equipe"
              type="text"
              fullWidth
            />
            <TextField
              value={description}
              onChange={e => setDescription(e.target.value)}
              required
              margin="dense"
              id="description"
              label="Descrição da sua equipe"
              type="text"
              fullWidth
            />

            <RadioGroup
              aria-label="Type"
              name="Type"
              value={type}
              row
              onChange={e => setType(e.target.value)}
              className={classes.radios}
            >
              <FormControlLabel
                value="Geral"
                control={<Radio color="primary" />}
                label="Geral"
              />

              <FormControlLabel
                value="Lobinho"
                control={<Radio color="primary" />}
                label="Lobinho"
              />
            </RadioGroup>

            {loading ? (
              <Grid item xs={12} className={classes.loading}>
                <CircularProgress />
              </Grid>
            ) : (
              <DialogActions className={classes.actions}>
                <Button onClick={() => handleClose()} color="primary">
                  Cancelar
                </Button>
                <Button type="submit" color="primary">
                  Criar
                </Button>
              </DialogActions>
            )}
          </form>
        </DialogContent>
      </Dialog>
    </div>
  );
}

export default withStyles(styles)(ModalCreateTeam);
