/* eslint-disable react/prop-types */
import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Paper, CircularProgress, withStyles } from "@material-ui/core/";

import styles from "./styles";
import { getTeam } from "../../store/modules/team/actions";

import TeamInfo from "../TeamInfo";
import NoTeamCoponent from "../NoTeamCoponent";

function TeamComponent({ classes }) {
  const dispatch = useDispatch();
  const team = useSelector(state => state.team.team);
  const user = useSelector(state => state.user.user);
  const loading = useSelector(state => state.team.loading);

  useEffect(() => {
    if (!team) {
      dispatch(getTeam(user.team));
    }
  }, [dispatch, team, user]);

  return (
    <>
      {loading ? (
        <Paper className={classes.root}>
          <CircularProgress className={classes.loading} />
        </Paper>
      ) : (
        <>{team ? <TeamInfo team={team} /> : <NoTeamCoponent />} </>
      )}
    </>
  );
}

export default withStyles(styles)(TeamComponent);
