/* eslint-disable no-unused-vars */
/* eslint-disable import/no-cycle */
/* eslint-disable no-restricted-globals */
import { takeLatest, call, put, all } from "redux-saga/effects";

import api from "../../../services/api";

import bucket from "../../index";

import {
  getTeamSuccess,
  getTeamFailure,
  createTeamSuccess,
  createTeamFailure,
  joiTeamSuccess,
  joiTeamFailure
} from "./actions";

export function* getTeam({ payload }) {
  try {
    const { teamId } = payload.team;

    const response = yield call(api.get, `team/${teamId}`);

    const team = response.data;

    yield put(getTeamSuccess({ team }));
  } catch (err) {
    yield put(getTeamFailure());
  }
}

export function* createTeam({ payload }) {
  try {
    const { user } = bucket.store.getState().user;

    const data = {
      teamName: payload.team.name,
      description: payload.team.description,
      groupName: user.groupName,
      groupNumber: user.groupNumber,
      type: payload.team.type,
      session: user.session,
      name: user.name,
      date: user.date
    };

    const response = yield call(api.post, `team/create`, data);

    yield put(createTeamSuccess());
  } catch (error) {
    yield put(createTeamFailure());
  }
}

export function* joinTeam({ payload }) {
  try {
    const { teamId } = payload;

    const response = yield call(api.post, `team/join`, { teamId });

    yield put(joiTeamSuccess());
  } catch (error) {
    yield put(joiTeamFailure());
  }
}

export default all([
  takeLatest("@team/JOIN_TEAM_REQUEST", joinTeam),
  takeLatest("@team/GET_TEAM_REQUEST", getTeam),
  takeLatest("@team/CREATE_TEAM_REQUEST", createTeam)
]);
