export function getTeam(team) {
  return {
    type: "@team/GET_TEAM_REQUEST",
    payload: { team }
  };
}

export function getTeamSuccess({ team }) {
  return {
    type: "@team/GET_TEAM_SUCCESS",
    payload: { team }
  };
}

export function getTeamFailure() {
  return {
    type: "@team/GET_TEAM_FAILURE"
  };
}

export function createTeam(team) {
  return {
    type: "@team/CREATE_TEAM_REQUEST",
    payload: { team }
  };
}

export function createTeamSuccess() {
  return {
    type: "@team/CREATE_TEAM_SUCCESS"
  };
}

export function createTeamFailure() {
  return {
    type: "@team/CREATE_TEAM_FAILURE"
  };
}

export function joiTeam(teamId) {
  return {
    type: "@team/JOIN_TEAM_REQUEST",
    payload: { teamId }
  };
}

export function joiTeamSuccess() {
  return {
    type: "@team/JOIN_TEAM_SUCCESS"
  };
}

export function joiTeamFailure() {
  return {
    type: "@team/JOIN_TEAM_FAILURE"
  };
}
