/* eslint-disable react/prop-types */
import React, { useState } from "react";
import {
  Paper,
  Typography,
  Button,
  withStyles,
  Grid
} from "@material-ui/core/";

import styles from "./styles";

import ModalJointTeam from "../ModalJoinTeam";
import ModalCreateTeam from "../ModalCreateTeam";

function NoTeamCoponent({ classes }) {
  const [joinTeam, setJoinTeam] = useState(false);
  const [createTeam, setCreateTeam] = useState(false);

  return (
    <Paper className={classes.root}>
      <Typography variant="h5" component="h3" align="center">
        Você ainda nao esta em uma equipe.
      </Typography>

      <Grid
        container
        spacing={2}
        direction="row"
        justify="space-around"
        alignItems="center"
        className={classes.divButtons}
      >
        <Grid item xs={12} sm={6} className={classes.divButton}>
          <Button
            variant="contained"
            size="large"
            color="primary"
            className={classes.button}
            onClick={() => setCreateTeam(!createTeam)}
          >
            Criar uma equipe
          </Button>
        </Grid>
        <Grid item xs={12} sm={6} className={classes.divButton}>
          <Button
            variant="contained"
            size="large"
            color="primary"
            className={classes.button}
            onClick={() => setJoinTeam(!joinTeam)}
          >
            Entrar em uma equipe
          </Button>
        </Grid>
      </Grid>

      <ModalCreateTeam open={createTeam} setOpen={setCreateTeam} />
      <ModalJointTeam open={joinTeam} setOpen={setJoinTeam} />
    </Paper>
  );
}

export default withStyles(styles)(NoTeamCoponent);
