import { produce } from "immer";

const INITIAL_STATE = {
  team: null,
  loading: false,
  createTeamLoading: false,
  joinTeamLoading: false
};

export default function team(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      case "@team/GET_TEAM_REQUEST": {
        draft.loading = true;
        break;
      }

      case "@team/GET_TEAM_SUCCESS": {
        draft.team = action.payload.team;
        draft.loading = false;
        break;
      }

      case "@team/GET_TEAM_FAILURE": {
        draft.loading = false;
        break;
      }

      case "@auth/SIGN_OUT_REQUEST": {
        draft.team = null;
        draft.loading = false;
        break;
      }

      case "@team/CREATE_TEAM_REQUEST": {
        draft.createTeamLoading = true;
        break;
      }

      case "@team/CREATE_TEAM_SUCCESS": {
        draft.createTeamLoading = false;
        break;
      }

      case "@team/CREATE_TEAM_FAILURE": {
        draft.createTeamLoading = false;
        break;
      }

      case "@team/JOIN_TEAM_REQUEST": {
        draft.joinTeamLoading = true;
        break;
      }

      case "@team/JOIN_TEAM_SUCCESS": {
        draft.joinTeamLoading = false;
        break;
      }

      case "@team/JOIN_TEAM_FAILURE": {
        draft.joinTeamLoading = false;
        break;
      }

      default:
    }
  });
}
