/* eslint-disable no-unused-vars */
/* eslint-disable react/prop-types */
import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Grid,
  CircularProgress,
  withStyles,
  RadioGroup,
  Radio,
  FormControlLabel
} from "@material-ui/core";
import api from "../../services/api";

import { joiTeam } from "../../store/modules/team/actions";

import styles from "./styles";

function ModalJoinTeam({ classes, open, setOpen }) {
  const dispatch = useDispatch();
  const user = useSelector(state => state.user.user);
  const loadingJoin = useSelector(state => state.team.joinTeamLoading);
  const [teams, setTeams] = useState([]);
  const [loading, setLoading] = useState(false);
  const [teamId, setTeamId] = useState("");

  async function handleOpen() {
    setLoading(true);
    const response = await api.get(`team/list-by-group/${user.groupNumber}`);
    setLoading(false);
    setTeams(response.data);
  }

  useEffect(() => {
    if (teams[0]) {
      setTeamId(teams[0].teamId);
    }
  }, [teams]);

  function handleClose() {
    setOpen(false);
  }

  function handleJoinTeam() {
    dispatch(joiTeam(teamId));
  }

  return (
    <div>
      <Dialog
        open={open}
        onEnter={() => handleOpen()}
        aria-labelledby="Modal join team"
        aria-describedby="Modal join team"
      >
        <DialogTitle id="alert-dialog-slide-title">
          Entrar em uma equipe
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-slide-description">
            Escolha entre uma das equipes do seu grupo
          </DialogContentText>

          {loading ? (
            <Grid item xs={12} className={classes.loading}>
              <CircularProgress />
            </Grid>
          ) : (
            <>
              {teams.length === 0 ? (
                <DialogContentText>
                  Seu grupo ainda nao tem nenhuma equipe , crie uma equipe e
                  comece essa aventura!
                </DialogContentText>
              ) : (
                <RadioGroup
                  aria-label="Type"
                  name="Type"
                  value={teamId}
                  onChange={e => setTeamId(e.target.value)}
                  className={classes.radios}
                >
                  {teams &&
                    teams.map(team => (
                      <FormControlLabel
                        key={team.teamId}
                        value={team.teamId}
                        control={<Radio color="primary" />}
                        label={team.name}
                      />
                    ))}
                </RadioGroup>
              )}
            </>
          )}
        </DialogContent>

        {loadingJoin ? (
          <Grid item xs={12} className={classes.loading}>
            <CircularProgress />
          </Grid>
        ) : (
          <DialogActions>
            <Button onClick={() => handleClose()} color="primary">
              Cancelar
            </Button>
            {loading || teams.length === 0 ? null : (
              <Button onClick={() => handleJoinTeam()} color="primary">
                Entrar
              </Button>
            )}
          </DialogActions>
        )}
      </Dialog>
    </div>
  );
}

export default withStyles(styles)(ModalJoinTeam);
