/* eslint-disable import/no-cycle */
import { all, call, put, takeLatest } from "redux-saga/effects";

import api from "../../../services/api";

import { getUserSuccess } from "./actions";

import bucket from "../../index";

export function* getUser() {
  console.log("logado");
  const { signed } = bucket.store.getState().auth;

  if (signed) {
    try {
      const response = yield call(api.get, `user`);

      yield put(getUserSuccess(response.data));
    } catch (error) {}
  }
}

export default all([
  takeLatest("@user/GET_USER_REQUEST", getUser),
  takeLatest("@team/CREATE_TEAM_SUCCESS", getUser),
  takeLatest("@team/JOIN_TEAM_SUCCESS", getUser)
]);
