/* eslint-disable import/no-cycle */
/* eslint-disable no-unused-vars */
/* eslint-disable no-restricted-globals */
import { takeLatest, call, put, all } from "redux-saga/effects";
import { toast } from "react-toastify";

import { PAUSE } from "redux-persist/es/constants";
import history from "../../../services/history";
import api from "../../../services/api";

import { signInSuccess, signFailure, signUpSuccess, signOut } from "./actions";
import { getUser } from "../user/actions";

import bucket from "../../index";

export function* signIn({ payload }) {
  try {
    let { register } = payload;
    const { date } = payload;

    if (!isNaN(register)) {
      register = `${register}@joti.com.br`;
    }

    const response = yield call(api.post, "user/login", { register, date });

    const { token, me } = response.data;
    const user = me;

    api.defaults.headers.Authorization = `Bearer ${token}`;

    yield put(signInSuccess(token, user));

    history.push("/team");
  } catch (err) {
    toast.error(
      "Erro, verifique se você esta inscrito na ativade ou se seus dados estao incorretos."
    );
    yield put(signFailure());
  }
}

export function DosignOut() {
  history.push("/");
}

export function* signUp({ payload }) {
  try {
    const { user, type } = payload;

    const { register } = user;

    if (type === "Externo") {
      const response = yield call(api.post, "/user/create/external", user);
    } else {
      const response = yield call(api.post, "/user/create/br", { register });
    }

    yield put(signUpSuccess());

    history.push("/");
  } catch (error) {
    if (error.response.data.error) {
      toast.error(error.response.data.error);
    } else {
      toast.error(error.response.data.message);
    }

    yield put(signFailure());
  }
}

export function setToken({ payload }) {
  if (!payload) return;

  const { token } = payload.auth;

  if (token) {
    api.defaults.headers.Authorization = `Bearer ${token}`;
  }
}

export function* controRedux() {
  const { signed } = bucket.store.getState().auth;

  if (signed) {
    yield put(getUser());
  } else {
    yield put(signOut());
  }
}

export default all([
  takeLatest("persist/REHYDRATE", setToken),
  takeLatest("@auth/SIGN_IN_REQUEST", signIn),
  takeLatest("@auth/SIGN_UP_REQUEST", signUp),
  takeLatest("@auth/SIGN_OUT_REQUEST", DosignOut),
  takeLatest("persist/REHYDRATE", controRedux)
]);
